@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lovers+Quarrel&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --main-color: rgb(48, 101, 136);
  --navbar-color:rgb(75, 75, 75);
  --selection-color: rgb(81, 134, 169);
}

html,body{min-height:100vh;}

#tsparticles {
  position : absolute;
  width:100%;
  height:100%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.home, .skills, .projects {
  min-height:100vh;
  width: 100%;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  background-color: rgb(48, 101, 136);
  background-color: var(--main-color);
  white-space: pre-wrap;
}

.bar {
  height: 2px;
  width: 60vw;
  background: white;
  margin:auto;
}

a {
  color: inherit !important;
  text-decoration: none;
}
.navbar {
    background-color:var(--main-color);
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
    z-index: 1;
  }
  
  .menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
    z-index: 1;
  }
  
  .nav-menu {
    background-color:var(--navbar-color);
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    z-index: 1;
  }
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
    z-index: 1;
  }
  
  .nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
    z-index: 1;
  }
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
    z-index: 1;
  }
  
  .nav-text a:hover {
    background-color: red;
    z-index: 1;
  }
  
  .nav-menu-items {
    width: 100%;
    z-index: 1;
  }
  
  .navbar-toggle {
    background-color: var(--navbar-color);
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
    z-index: 1;
  }
  
.title {
    text-align: center;
    padding: 10px 0;
    color: #fff;
    font-weight: 100;
    z-index: 1;
}

.social {
    text-align: center;
    display: flex;
    padding-top: 3vh;
    justify-content: center;
    z-index: 1;
}

.icon {
    width: 100px;
    text-align: center;
    margin:0 2vw;
    text-decoration: none;
    z-index: 1;
    color: #fff;
    font-size: 1.3em;
}

.icon a{
    color: inherit !important;
    text-decoration: none;
}

.icon:hover { 
    color:var(--selection-color)
}

.text {
    justify-content: center;
    text-align: center;
    color: #fff;
    font-weight: 100;
    font-size: 0.70em;
    padding:2vw 5vw;
    margin: auto;
}

.buttonContainer {
    width: 100%;
    justify-content: center;
    text-align: center;
}
.image {
    float: left;    
    /* border-radius: 25px; */
    margin: 5vh 0vw;
    margin-right: 2vw;
    /* box-shadow: 5px 10px #311818; */
    width: 50vw;
}

.textContainer {
    padding-top: 10vh;
    text-align: left;
    padding-right: 5vw;
    color: white;
}

.quote {
    margin: auto;
    font-family: 'Lovers Quarrel', cursive;
    font-size: 3rem;
}

.card-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.tags {
    padding: 3px 7px 5px 7px;
    background-color: rgb(81, 134, 169);
    border-radius: 25px;
    margin: 0 4px 0 4px;
    color: #fff;
    font-size: 11px;
}

.tag-container {
    margin-top: 10px;
}

